@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
header {
	font-family: Pacifico;
	text-align: center;
	color: #424242;
	box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.1);
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background: #fff;
	z-index: 10;
}

header > div > div > a {
	margin-right: auto !important;
	margin-left: auto !important;
}
a > strong,
svg {
	color: #424242;
	font-size: 2rem;
}

button {
	font-family: Pacifico;
}

.content {
  max-width: 1200px;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.error {
  background-color: lightsalmon;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid tomato;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  padding: 25px;
  grid-gap: 25px;
  grid-auto-flow: dense;
  align-items: stretch;
  width: 100%;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2px;
}

.item-1 {
  grid-row: span 1;
}

.item-2 {
  grid-row: span 2;
}

.item-3 {
  grid-row: span 3;
}

.item-4 {
  grid-row: span 4;
}

.item-5 {
  grid-row: span 5;
}

.item {
  position: relative;
}

